.project-tabs {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
}

.project-tab {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    line-height: 50px;
    font-weight: 700;
    font-size: 17px;
    text-align: center;
    width: 180px;
    transition: .3s;
    position: relative;
    margin-left: 0;
    margin-right: 0;
    margin-left: 0;
    margin-right: 0;
    z-index: 2;
}

.project-tab::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    z-index: 2;
}

.active-project::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ef224b;
    z-index: 2;
}

.active-project {
    color: #ef224b;
}

.projects {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-self: center;
    z-index: 2;
}

.All, .Other, .Web, .Mobile {
    display: grid;
    /* grid-template-rows: repeat(auto-fit,minmax(400px, 1fr)); */
    grid-template-columns: repeat(auto-fit, minmax(23vw, 1fr));
    grid-gap: 2vw;
    gap: 2vw;
    margin-top: 50px;
    width: 100%;
}

.project-card {
    justify-self: stretch;
    position: relative;
    height: 20vw;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    overflow: hidden;
    z-index: 2;
}

.project-card img {
    width: 100%;
    height: 15vw;
    object-fit: cover;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    transition: .3s ease-in;
    transition-property: transform;
}

.project-card:hover img {
    transform: scale(1);
}

.project-card:hover .card-content {
    height: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.card-content {
    height: 6vw;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #303a74;
    transition: .4s ease-in;
    overflow: hidden;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    text-align: left;
    padding-left: 1.5vw;
}

